const StyledPerfectScrollbar = styled(PerfectScrollbar)`
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  overflow-x: scroll;
  z-index: 999;
  cursor: pointer !important;

  .ps__rail-x {
      display: none;
  }

  .ps__rail-y {
    display: none;
}
  
  div:nth-child(1) {
    display: none !important;
    width: auto !important;
  }
`

const StyledInfiniteScroll = styled.div`
  position: relative;
  overflow-y: hidden;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  z-index: 1;
  cursor: pointer !important;

  div:nth-child(1) {
    display: none !important;
    height: 0px;
  }
  
  }
`


const StyledImg = styled(Img)`
    width: 100%;
    height: 100%;
    margin: 0 75px;
    padding: 0;
`

const StyledBasicImg = styled.img`
    height: 100%;
    margin: 0 25px;
    padding: 0;
`;

const ZoomDiv = posed.div({
    'open': {
    //  applyAtStart: { scale: 2 },
     position:'fixed',
     top: (props) => (props.windowHeight / 2) - 100,
     scale: 2,
     transition: {ease: [0.08, 0.69, 0.2, 0.99], duration: 600 },
     flip: true,
    },

    'closed': {
     applyAtStart: { scale: 1 },
     applyAtEnd: { scale: 1 },
    //  scale: 1,
     position:'relative',
     top: 'auto',
     transition: {ease: [0.08, 0.69, 0.2, 0.99], duration: 300 },
     flip: true
    }
});

const StyledZoomDiv = styled(ZoomDiv)`
    height: 150px; 
    width: 100%; 
    z-index: 999;

    @media(min-width: 992px) {
        height: 200px; 
    }
`

const PerfectScrollbarHolder = styled.div`
    position: relative;
    height: 150px;
    margin: 200px 0;
    // border-top: 1px solid black;
    // border-bottom: 1px solid black;
    padding: 25px 0;

    @media(min-width: 992px) {
        height: 200px; 
    }
    
`

const Frame = posed.div({
    open: {
      applyAtStart: { display: 'block' },
      opacity: 1
    },
    closed: {
      applyAtEnd: { display: 'none' },
      opacity: 0
    }
  });

const StyledFrame = styled(Frame)`
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: none;
    background: rgb(235,51,35);
    transform: translateZ(0) translateY(-50%);
    height: 100vh;
    z-index: -1;
`;


