class IndexPage extends React.Component {
  constructor(props){
    super(props);

    this.state = {
      collections: [],
    }

    this.allCollections = [];

    this.handleScroll = this.handleScroll.bind(this);
    this.handleThrottledScroll = _.throttle(this.handleThrottledScroll.bind(this), 200);

  }

  componentWillMount(){
    console.log(this.props.data);

    let collectionFetchData = function(data) {
      this.projectName = "";
      this.projectYear = "";
      this.images = data;
    }

    // let collectionData = new collectionFetchData(this.props.data);

    // this.props.data.map((data) => {
    //   return allCollections.push(new collectionFetchData(data));
    // });

    this.allCollections = [Object.values(this.props.data), Object.values(this.props.data), Object.values(this.props.data)];

    this.setState({
      collections: this.allCollections
    });


  }

  componentDidMount() {

    console.log(this.state);

    this.container = this.infiniteScroll;

    window.addEventListener('scroll', this.handleScroll);

  }

  addElements = () => {
    
    this.setState(prevState => ({
      collections: [...prevState.collections, ...this.allCollections]
    }));

  }

  handleScroll = () => {
    
    // console.log(window.scrollY);
    // console.log(this.container.clientHeight);
    // console.log(window.innerHeight);

    this.handleThrottledScroll();

}

handleThrottledScroll = () => {

  if(this.container.scrollHeight - window.scrollY  == 0  ) {

    this.addElements();

    console.log('end of page!');

  }
  
}

  render(){

    return(
      <>
      <SEO title="Anna Genger" keywords={[`Anna`, `Genger`, `Projects`]} />
      <BackgroundImage src={Logo}/>
      <ProjectDiv ref={div => this.infiniteScroll = div}>
      {/* <div ref={div => this.infiniteScroll = div} onScroll={this.handleScroll}> */}
        <BackgroundImage2 src={Logo}/>
        {/* <InfiniteSlider data={this.state.collections}/>
        <InfiniteSlider data={this.state.collections}/>
        <InfiniteSlider data={this.state.collections}/> */}
        {/* <InfiniteSlider data={this.state.collections}/> */}

        {
          this.state.collections.map((item, index) => {
            return (<InfiniteSlider data={item}/>)
          })
        }
        {/* </div> */}
      </ProjectDiv>
    </>
    )
  }
}

export default IndexPage

